        v-else
      >
        <p>{{$t('pop.line2')}}</p>
        <span>{{wholeWallet}}</span>
        <div
          class="log_out"
          @click="logoutWallet"
        >
          {{$t('pop.line3')}}
        </div>
      </div>
    </MobilePop>
