<template>
  <div class="stake">
    <header id="parent_id">
      <div
        class="left"
        :style="{width:leftWidth}"
      >
        <div class="title">
          {{$t('stake.i1')}}
        </div>
        <div class="text">
          {{$t('stake.i2')}}
        </div>

        <p>HOS</p>

        <div class="bis">
          <div
            :class="{active:item.id === current_bi.id}"
            v-for="(item,index) in hos_nams"
            @click="setCurrentBi(item)"
            :key="index"
          >{{item.name}}</div>
        </div>

        <p>SHOS</p>

        <div class="bis">
          <div
            :class="{active:item.id === current_bi.id}"
            v-for="(item,index) in shos_names"
            @click="setCurrentBi(item)"
            :key="index"
          >{{item.name}}</div>
        </div>
      </div>

      <div class="right">
        <div class="top">
          <div>
            <p>TVL</p>
            <i :title="tvl_num">${{tvl_num}}</i>
          </div>
          <div>
            <p>HOS Price</p>
            <i :title="hos_price">${{hos_price}}</i>
          </div>
          <div>
            <p>APR</p>
            <span>264.57%</span>
          </div>
        </div>
        <div class="bottom">
          <div class="left">
            <p>{{$t('stake.i3')}}</p>
            {{$t('stake.i4')}}
          </div>
          <div
            class="right"
            @click="openNewPage('https://pancakeswap.finance/swap')"
          ></div>
        </div>

        <div
          class="box"
          :id="item.name"
          v-for="(item) in bis_list"
          :key="item.id"
        >
          <div class="title">
            <img
              :src="item.logo"
              :class="item.class_name"
            >
            {{item.name}}
          </div>
          <p>
            <span>{{$t('stake.i5')}}：</span>
            <span>{{item.apr_num}}%</span>
          </p>
          <p>
            <span>{{$t('stake.i6')}}：</span>
            <span>{{item.earn_type || 'HOS'}}</span>
          </p>
          <div class="hos_earned">{{$t('stake.i7')}} {{item.earn_type || 'HOS'}}</div>
          <div class="earn_num">{{item.earn_num}}</div>
          <!-- 提取收益 -->
          <div
            :class="{harvest:true, active:item.earn_num>0}"
            @click="Harvest(item)"
            v-if="isConnect&& item.enable"
          >{{$t('stake.i10')}}</div>
          <div class="staked">{{item.name}} {{$t('stake.i8')}}</div>
          <div class="staked_num">{{item.staked_num}}</div>
          <!-- 提取本金 -->
          <div
            :class="{with_draw:true,active:item.staked_num>0}"
            @click="withDrawClick(item)"
            v-if="isConnect && item.enable"
          >{{$t('stake.i13')}}</div>
          <!-- 质押 -->
          <div
            class="stake_button active"
            @click="openStakePop(item)"
            v-if="isConnect&& item.enable"
          >{{$t('stake.i12')}}</div>
          <div
            class="long_button"
            v-if="!isConnect"
            @click="connectWallet"
          >{{$t('stake.i9')}}</div>
          <div
            class="long_button"
            v-if="item.name !== 'BNB' && isConnect && !item.enable"
            @click="enableContractToHos(item)"
          >{{$t('stake.i11')}}</div>
        </div>
      </div>
    </header>

    <!-- 质押代币 -->
    <POP
      v-if="stake_pop_show"
      @close='stake_pop_show = false'
    >
      <div class="stake_pop">
        <div class="title">{{$t('pop.line4')}}</div>
        <div class="detail">
          <p>
            <span>{{$t('stake.i12')}}</span>
            <span>{{$t('pop.line5')}} ：{{current_stake_bi.balance}}</span>
          </p>
          <div>
            <input v-model="current_stake_bi.stake_howmuch">
            <div>
              <span @click="setStakeMax(current_stake_bi)">MAX</span>
              {{current_stake_bi.name}}
            </div>
          </div>
        </div>
        <div class="buttons">
          <div
            class="cancel"
            @click='stake_pop_show = false'
          >{{$t('pop.line6')}}</div>
          <div
            class="confirm"
            @click="STAKE(current_stake_bi)"
          >{{$t('pop.line7')}}</div>
        </div>
      </div>
    </POP>

    <!-- 提取本金 -->
    <POP
      v-if="withdraw_pop_show"
      @close='withdraw_pop_show = false'
    >
      <div class="stake_pop">
        <div class="title">{{$t('stake.i13')}}</div>
        <div class="detail">
          <p>
            <span>{{$t('stake.i13')}}</span>
            <span>{{$t('pop.line5')}} ：{{current_stake_bi.balance}}</span>
          </p>
          <div>
            <input v-model="current_stake_bi.with_draw_howmuch">
            <div>
              <span @click="current_stake_bi.with_draw_howmuch=current_stake_bi.staked_num">MAX</span>
              {{current_stake_bi.name}}
            </div>
          </div>
        </div>
        <div class="buttons">
          <div
            class="cancel"
            @click="withdraw_pop_show = false"
          >{{$t('pop.line6')}}</div>
          <div
            class="confirm"
            @click="WithDrawClick(current_stake_bi)"
          >{{$t('pop.line7')}}</div>
        </div>
      </div>
    </POP>
  </div>
</template>

<script>
import POP from '@/components/Pop'

export default {
  name: 'Stake',
  components: { POP },
  props: {
    isConnect: {
      type: Boolean
    }
  },
  watch: {
    isConnect: {
      immediate: true,
      handler (vale) {
        if (vale) {
          this.getTVLAndPrice()

          this.getTokenAmount()

          this.allowanceUsdt()

          this.loop_search = setInterval(() => {
            this.firstSort = false
            this.getTokenAmount()
          }, 3000)

        } else {
          if (this.loop_search) clearInterval(this.loop_search)
          this.tvl_num = 0
          this.hos_price = 0
        }
      }
    }
  },
  beforeDestroy () {
    if (this.loop_search) clearInterval(this.loop_search)
    window.removeEventListener('resize', this.onResize);
  },
  data () {
    return {
      leftWidth: '',
      loop_search: null,
      fullscreenLoading: null,
      bis_list: [
        {
          id: 0,
          logo: require('../../assets/images/bnb.png'),
          name: 'BNB',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'bnbContractInstance',
          addr_name: 'bnbAddr',
          enable: true,
          balance: 0,
          apr_num: 95.89
        },
        {
          id: 1,
          logo: require('../../assets/images/usdt.png'),
          name: 'USDT',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'usdtTokenContractInstance',
          addr_name: 'usdtAddr',
          enable: false,
          balance: 0,
          apr_num: 95.89
        },
        {
          id: 2,
          logo: require('../../assets/images/busd.png'),
          name: 'BUSD',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'busdTokenContractInstance',
          addr_name: 'busdAddr',
          enable: false,
          balance: 0,
          apr_num: 95.89
        },
        {
          id: 3,
          logo: require('../../assets/images/hos.png'),
          name: 'HOS',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'hosContractInstance',
          addr_name: 'hosAddr',
          enable: false,
          balance: 0,
          apr_num: 131.51
        },
        {
          id: 4,
          name: 'HOS-USDT LP',
          logo: require('../../assets/images/hos_usdt.png'),
          class_name: 'db_img',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'lpContractInstance',
          addr_name: 'lpAddr',
          enable: false,
          balance: 0,
          apr_num: 264.57
        },
        {
          id: 5,
          name: 'HOS-USDT LP',
          logo: require('../../assets/images/hos_usdt.png'),
          earn_type: 'SHOS',
          class_name: 'db_img',
          earn_num: 0,
          staked_num: 0,
          stake_howmuch: 0,
          with_draw_howmuch: 0,
          instance: 'lpContractInstance',
          addr_name: 'lpAddr',
          enable: false,
          balance: 0,
          apr_num: 131.51
        }
      ],
      hos_nams: [],
      shos_names: [],
      current_bi: {},
      current_stake_bi: {},
      stake_pop_show: false,
      withdraw_pop_show: false,
      tvl_num: 0,
      hos_price: 0,
      firstSort: true
    }
  },
  methods: {
    // 跳转新页面
    openNewPage (url) {
      window.open(url)
    },
    // 获取质押最大金额
    setStakeMax (item) {
      item.stake_howmuch = String(item.balance)
    },
    // 确认质押
    STAKE (item) {
      if (!item.stake_howmuch) {
        this.$message.error('质押数量要大于0！')
        return
      }
      const that = this
      this.fullscreenLoading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
      this.stake_pop_show = false
      var amount = item.stake_howmuch;//就是用户输入的金额
      if (item.id === 0) {
        // bnb
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');
        window.hosContractInstance.methods.pledgeBnb().send({ from: window.myWallet, value: exchangeAmount }, function (error, result) {
          console.log("抵押BNB操作结果", result);
        }).catch(console.log).then(
          function (depositRes) {
            console.log("BNB抵押交易完毕", depositRes);
            //抵押成功后调接口
            that.$axios.get(`http://47.243.134.145:12138/add?address=${localStorage.getItem('myWallet')}&coin=${item.name.replace(/[\s-]/g, '').toLowerCase()}`)

            that.fullscreenLoading.close()

            that.getTokenAmount()

          }
        )
      } else if (item.id === 1 || item.id === 2 || item.id === 3 || item.id === 4) {
        // 普通币
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');

        window.hosContractInstance.methods.pledge(window[item.addr_name], exchangeAmount).send({ from: window.myWallet }, function (error, result) {
          console.log("抵押ERC20操作结果", result);
          if (error) that.fullscreenLoading.close()
        }).catch(console.log).then(
          function (depositRes) {
            console.log("ERC20抵押交易完毕", depositRes);
            console.log('tokenadr', window[item.addr_name], 'amount', amount)

            that.fullscreenLoading.close()
            //抵押成功后调接口 get请求
            //address 当前用户的地址  coin当前币种的英文名称 都是小写 注意lp不要有空格和- 写成 hosusdtlp
            that.$axios.get(`http://47.243.134.145:12138/add?address=${localStorage.getItem('myWallet')}&coin=${item.name.replace(/[\s-]/g, '').toLowerCase()}`)
          }
        )
      } else {
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');
        window.shosContractInstance.methods.pledge(window[item.addr_name], exchangeAmount).send({ from: window.myWallet }, function (error, result) {
          console.log("抵押ERC20操作结果", result);
          if (error) that.fullscreenLoading.close()
        }).catch(console.log).then(
          function (depositRes) {
            console.log("ERC20抵押交易完毕", depositRes);
            console.log('tokenadr', window[item.addr_name], 'amount', amount)

            that.fullscreenLoading.close()
            //抵押成功后调接口 get请求
            //address 当前用户的地址  coin当前币种的英文名称 都是小写 注意lp不要有空格和- 写成 hosusdtlp
            that.$axios.get(`http://47.243.134.145:12138/add?address=${localStorage.getItem('myWallet')}&coin=${item.name.replace(/[\s-]/g, '').toLowerCase()}`)
          }
        )
      }
    },
    // 提取本金弹窗
    withDrawClick (item) {
      this.getBiBalance(item)
      item.with_draw_howmuch = 0
      this.current_stake_bi = item
      this.withdraw_pop_show = true
    },
    // 确认提取本金
    WithDrawClick (item) {
      if (!item.with_draw_howmuch) {
        this.$message.error('提取数量要大于0！')
        return
      }
      const that = this
      this.fullscreenLoading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });

      this.withdraw_pop_show = false
      var amount = item.with_draw_howmuch;//就是用户输入的金额
      // bnb
      if (item.id === 0) {
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');
        window.hosContractInstance.methods.withdrawBnb(exchangeAmount).send({ from: window.myWallet }, function (error, result) {
          // console.log("提取已质押BNB操作错误", error);
          if (error) that.fullscreenLoading.close()
          console.log("提取已质押BNB操作结果", result);
        }).catch(console.log).then(
          function (depositRes) {
            console.log("提取已质押BNB完毕", depositRes);

            that.getTokenAmount()
            that.fullscreenLoading.close()
          }
        )
      } else if (item.id === 1 || item.id === 2 || item.id === 3 || item.id === 4) {
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');
        //提取其他erc20币种，就换第一个参数
        window.hosContractInstance.methods.withdraw(window[item.addr_name], exchangeAmount).send({ from: window.myWallet }, function (error, result) {
          console.log("提取已质押ERC20操作错误", error);
          console.log("提取已质押ERC20操作结果", result);
          if (error) that.fullscreenLoading.close()
        }).catch(console.log).then(
          function (depositRes) {
            console.log("提取已质押ERC20完毕", depositRes);
            that.fullscreenLoading.close()
          }
        )
      } else {
        let exchangeAmount = window.web3js.utils.toWei(amount, 'ether');
        //shos
        window.shosContractInstance.methods.withdraw(window[item.addr_name], exchangeAmount).send({ from: window.myWallet }, function (error, result) {
          console.log("提取已质押ERC20操作错误", error);
          console.log("提取已质押ERC20操作结果", result);
          if (error) that.fullscreenLoading.close()
        }).catch(console.log).then(
          function (depositRes) {
            console.log("提取已质押ERC20完毕", depositRes);
            that.fullscreenLoading.close()
          }
        )
      }
    },
    // 提取收益
    Harvest (item) {
      const that = this
      this.fullscreenLoading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });

      // 如果是HOS，用HOS实例,否则用SHOS实例,参数就是对应币种的地址
      if (item.id !== 5) {
        window.hosContractInstance.methods.getLP(window[item.addr_name]).send({ from: window.myWallet }, function (error, result) {
          if (error) that.fullscreenLoading.close()
          console.log("提取操作结果", result);

          that.getTokenAmount()
        }).then(
          function () {
            that.fullscreenLoading.close()
          }
        );
      } else {
        window.shosContractInstance.methods.getLP(window[item.addr_name]).send({ from: window.myWallet }, function (error, result) {
          if (error) that.fullscreenLoading.close()
          console.log("提取操作结果", result);

          that.getTokenAmount()
        }).then(
          function () {
            that.fullscreenLoading.close()
          }
        );
      }

    },
    // 设置当前点击的是哪个币
    setCurrentBi (item) {
      this.current_bi = item
      this.bis_list.map((bis, index) => {
        if (bis.id === item.id) {
          this.bis_list.splice(index, 1)
          this.bis_list.unshift(item)
        }
      })
      document.body.scrollTop = 0
    },
    // 各币种授权划转权限,bnb不需要授权
    enableContractToHos (item) {
      const that = this
      this.fullscreenLoading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });

      var bi_instance = item.instance

      if (item.id === 1 || item.id === 2 || item.id === 3 || item.id === 4) {
        // HOS授权
        window[bi_instance].methods.approve(window.hosAddr, window.approveAmount).send({ from: window.myWallet })
          .on('transactionHash', function (hash) {
            //一转账就会产生hash也就是txid,产生了不代表一定成功，下面的完毕才是成功，成功后访问
            //https://bscscan.com/tx/0xd920e908f24f95ebfbcc599fd6ee990f45029ce16b882e865735d88b10c3634d  后面就是txid
            console.log('转移到erc20抵押交易授权transactionHash', hash)
            console.log('访问此链接查看真实交易情况', "https://bscscan.com/tx/" + hash)
          })
          .on('confirmation', function (confirmationNumber, receipt) {
            //确认会有很多步骤，因此会打很多日志
            console.log('转移到erc20抵押交易授权confirmation', confirmationNumber, receipt)
          })
          .on('receipt', function (receipt) {
            // receipt example
            console.log("转移到erc20抵押交易授权receipt", receipt);
          })
          .on('error', function (err) {
            // receipt example
            //关闭转圈，弹出错误
            that.$message.error(err)
            that.fullscreenLoading.close()
          }).then(
            function (res) {
              console.log("授权交易完毕,交易信息", res);
              that.fullscreenLoading.close()
              item.enable = true
            }
          );
      } else {
        window[bi_instance].methods.approve(window.shosAddr, window.approveAmount).send({ from: window.myWallet })
          .on('transactionHash', function (hash) {
            //一转账就会产生hash也就是txid,产生了不代表一定成功，下面的完毕才是成功，成功后访问
            //https://bscscan.com/tx/0xd920e908f24f95ebfbcc599fd6ee990f45029ce16b882e865735d88b10c3634d  后面就是txid
            console.log('转移到erc20抵押交易授权transactionHash', hash)
            console.log('访问此链接查看真实交易情况', "https://bscscan.com/tx/" + hash)
          })
          .on('confirmation', function (confirmationNumber, receipt) {
            //确认会有很多步骤，因此会打很多日志
            console.log('转移到erc20抵押交易授权confirmation', confirmationNumber, receipt)
          })
          .on('receipt', function (receipt) {
            // receipt example
            console.log("转移到erc20抵押交易授权receipt", receipt);
          })
          .on('error', function (err) {
            // receipt example
            //关闭转圈，弹出错误
            that.$message.error(err)
            that.fullscreenLoading.close()
          }).then(
            function (res) {
              console.log("授权交易完毕,交易信息", res);
              that.fullscreenLoading.close()
              item.enable = true
            }
          );
      }
    },
    // 弹出质押窗口
    openStakePop (biType) {
      this.getBiBalance(biType)
      this.stake_pop_show = true
    },
    // 获取各币种余额
    getBiBalance (biType) {
      biType.stake_howmuch = 0
      this.current_stake_bi = biType
      // bnb
      if (biType.id === 0) {
        window[biType.instance].getBalance(window.myWallet, function (error, result) {
          //fromWei必须使用，用来格式化
          console.log(`${biType.name}余额转化后的结果`, window.web3js.utils.fromWei(result, 'ether'));
          const balance = Number(window.web3js.utils.fromWei(result, 'ether'))
          biType.balance = Math.floor(balance * 100) / 100
        });
      } else {
        // 其他币
        window[biType.instance].methods.balanceOf(window.myWallet).call({ from: window.myWallet }, function (error, result) {
          console.log(`${biType.name}余额转化后的结果`, window.web3js.utils.fromWei(result, 'ether'));
          const balance = Number(window.web3js.utils.fromWei(result, 'ether'))
          biType.balance = Math.floor(balance * 100) / 100
        });
      }
    },
    // 获取TVL和价格
    getTVLAndPrice () {
      const that = this
      window.hosContractInstance.methods.getStatus().call({ from: window.myWallet }, function (error, result) {
        //这个TVL加上下面那个SHOSTVL就总的，页面上显示总的
        // 获取HOS的TVL结果
        that.tvl_num = result[0]
        // 获取HOS的价格
        that.hos_price = result[1]

        window.shosContractInstance.methods.getStatus().call({ from: window.myWallet }, function (error, result) {
          // 获取SHOS的TVL结果
          that.tvl_num += result[0]
        });
      });
    },
    // 获取质押后可领取的hos收益
    getLpAmount () {
      this.bis_list.map(item => {
        if (item.id !== 5) {
          window.hosContractInstance.methods.getLpAmount(window[item.addr_name]).call({ from: window.myWallet }, function (error, result) {
            // console.log("获取usdt已质押币种格式化后", window.web3js.utils.fromWei(result, 'ether'));
            const num = Number(window.web3js.utils.fromWei(result, 'ether'))
            item.earn_num = Math.floor(num * 100) / 100

          });
        } else {
          //shos
          window.shosContractInstance.methods.getLpAmount(window[item.addr_name]).call({ from: window.myWallet }, function (error, result) {
            // console.log("获取usdt已质押币种格式化后", window.web3js.utils.fromWei(result, 'ether'));
            const num = Number(window.web3js.utils.fromWei(result, 'ether'))
            item.earn_num = Math.floor(num * 100) / 100
          });
        }
      })
    },
    // 获取各币种质押的数量
    getTokenAmount () {
      this.bis_list.map(item => {
        if (item.id !== 5) {
          // hos
          window.hosContractInstance.methods.getTokenAmount(window[item.addr_name]).call({ from: window.myWallet }, function (error, result) {
            // console.log("获取usdt已质押币种格式化后", window.web3js.utils.fromWei(result, 'ether'));
            item.staked_num = window.web3js.utils.fromWei(result, 'ether')
          });
        } else {
          //shos
          window.shosContractInstance.methods.getTokenAmount(window[item.addr_name]).call({ from: window.myWallet }, function (error, result) {
            // console.log("获取usdt已质押币种格式化后", window.web3js.utils.fromWei(result, 'ether'));
            item.staked_num = window.web3js.utils.fromWei(result, 'ether')
          });
        }
      })
      this.getLpAmount()
    },
    // 查询是否已经授权支付过
    allowanceUsdt () {
      this.bis_list.map(item => {
        //第一个参数 当前钱包地址 第二个参数 向哪个合约地址授权的
        if (item.id === 1 || item.id === 2 || item.id === 3 || item.id === 4) {
          // 一般币
          window[item.instance].methods.allowance(window.myWallet, window.hosAddr).call({ from: window.myWallet }, function (error, result) {
            //result>0表示已授权
            console.log(`获取${item.name}是否已授权`, window.web3js.utils.fromWei(result, 'ether'));
            item.enable = window.web3js.utils.fromWei(result, 'ether') > 0
          });

        } else if (item.id === 5) {
          //shos
          window[item.instance].methods.allowance(window.myWallet, window.shosAddr).call({ from: window.myWallet }, function (error, result) {
            //result>0表示已授权
            console.log(`获取${item.name}是否已授权`, window.web3js.utils.fromWei(result, 'ether'));
            item.enable = window.web3js.utils.fromWei(result, 'ether') > 0
          });
        }

        // 排序、暂不支持
        /* setTimeout(() => {
          if (this.firstSort) {
            console.log(999)
            this.bis_list.map((item, index) => {
              if (item.enable) {
                this.bis_list.splice(index, 1)
                this.bis_list.unshift(item)
              }
            })
          }
        }, 100); */
      })
    },
    // 链接钱包弹
    connectWallet () {
      this.$emit('connectWalletShow')
    },
    // 窗口变化
    onResize () {
      const parentWidth = document.getElementById('parent_id').offsetWidth
      this.leftWidth = parentWidth * 0.5 + 'px'
    }
  },
  mounted () {
    this.current_bi = this.bis_list[0]
    this.hos_nams = this.bis_list.filter((item, index) => {
      return index < 5
    })
    this.shos_names = this.bis_list.filter((item, index) => {
      return index === 5
    })
    this.onResize()
    window.addEventListener('resize', this.onResize);
  }
}
</script>

