<script>
// @ is an alias to /src
import POP from '@/components/Pop'

export default {
  name: 'Index',
  components: { POP },
  computed: {
    currentRoute () {
      return this.$route.name
    },
    currentLanguage () {
      return this.$i18n.locale
    }
  },
  data () {
    return {
      user: 'Connect',
      show_language_select: false,
      popShow: false,
      isConnect: false,
      wholeWallet: ''
    }
  },
  methods: {
    // 跳转新页面
    openNewPage (url) {
      window.open(url)
    },
    // 切换语言
    changeLanguage (language) {
      this.$i18n.locale = language
    },
    routerPath (path) {
      this.$router.push(path)
    },
    // 链接钱包
    connetWallet () {
      const that = this

      if (window.ethereum) {
        window.web3Provider = window.ethereum;
        // 新版需要请求用户授权
        window.ethereum.enable().catch(function () {
          alert("用户取消了授权");
          return
        }).then(function (res) {
          if (res) {
            that.connetCallBack()
          }
        })
      } else if (typeof window.web3 !== 'undefined') {
        // MetaMask Legacy dapp browsers...
        window.web3Provider = window.web3.currentProvider;
        that.connetCallBack()
      } else {
        alert('请先安装MetaMask');
        return false;
      }
    },
    // 授权后回调
    connetCallBack () {
      const that = this

      window.web3js = new window.Web3(window.web3Provider);
      //获取钱包地址
      window.web3js.eth.getAccounts(function (error, result) {
        if (!error) {
          that.popShow = false
          window.myWallet = result[0];
          localStorage.setItem('myWallet', result[0])
          that.wholeWallet = result[0];
          if (window.myWallet) {
            that.user = `${window.myWallet.substring(0, 6)}***${window.myWallet.substring(window.myWallet.length - 4)}`
            that.isConnect = true
            // console.log("我的钱包地址", window.myWallet);
          }
        } else {
          // console.log("我的钱包地址获取地址失败");
        }
      });

      //bnb地址的实例
      window.bnbContractInstance = window.web3js.eth;
      //usdt地址的实例
      window.usdtTokenContractInstance = new window.web3js.eth.Contract(window.simpleTokenABI.abi, window.usdtAddr);
      //busd地址实例
      window.busdTokenContractInstance = new window.web3js.eth.Contract(window.simpleTokenABI.abi, window.busdAddr);
      //hos地址的实例
      window.hosContractInstance = new window.web3js.eth.Contract(window.hosTokenABI.abi, window.hosAddr);
      //lp地址的实例
      window.lpContractInstance = new window.web3js.eth.Contract(window.hosTokenABI.abi, window.lpAddr);
      // shos实例
      window.shosContractInstance = new window.web3js.eth.Contract(window.hosTokenABI.abi, window.shosAddr);
    },
    // 退出钱包
    logoutWallet () {
      this.isConnect = false
      this.popShow = false
      this.wholeWallet = ''

      window.myWallet = ''
      this.user = 'Connect'
      localStorage.removeItem('myWallet')
    }
  },
  created () {
    const isLogin = localStorage.getItem('myWallet') && localStorage.getItem('myWallet') !== 'undefined'
    if (isLogin) {
      this.connetWallet()
    }
  }
}
</script>
